import React from 'react';
import ReactDOM from 'react-dom';
import './index.css';
import App from './App';
import registerServiceWorker from './registerServiceWorker';
import {HashRouter} from 'react-router-dom';
import {Provider} from 'react-redux';
import {createStore, combineReducers, applyMiddleware, compose} from 'redux';
import thunk from 'redux-thunk';
import firebase from 'firebase/app';
import 'firebase/database';
import 'firebase/auth';
import authReducer from './store/Reducers/authReducer';
import apparelReducer from './store/Reducers/apparelReducer';
import gameReducer from './store/Reducers/gameReducer';
import toyReducer from './store/Reducers/toyReducer';
import navbarReducer from './store/Reducers/navbarReducer';
import cartReducer from './store/Reducers/cartReducer';

var config = {
    apiKey: "AIzaSyBS7MErjiCho_gle7A8RX2MsyK3re6WUPM",
    authDomain: "allbuy-restore.firebaseapp.com",
    databaseURL: "https://allbuy-restore.firebaseio.com",
    projectId: "allbuy-restore",
    storageBucket: "allbuy-restore.appspot.com",
    messagingSenderId: "265223880567"
  };
firebase.initializeApp(config);

const rootReducer = combineReducers({
    auth: authReducer,
    apparel: apparelReducer,
    game: gameReducer,
    toy: toyReducer,
    navbar: navbarReducer,
    cart: cartReducer
})

const composeEnhancers = window.__REDUX_DEVTOOLS_EXTENSION_COMPOSE__ || compose;

const store = createStore(rootReducer, composeEnhancers(applyMiddleware(thunk)));

const app = (
    <Provider store={store}>
        <HashRouter basename={process.env.PUBLIC_URL}>
            <App />
        </HashRouter>
    </Provider>
)

ReactDOM.render( app, document.getElementById('root'));
registerServiceWorker();



